@use 'variables' as v;

html {
  font-size: 62.5%;
  box-sizing: border-box;
  font-family: 'Kumbh Sans', sans-serif;
  height: 100%;
  background: linear-gradient(v.$soft-violet, v.$soft-blue) no-repeat, v.$soft-blue;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

a:visited, a:link {
  color: white;
}

details summary { list-style-type: none; } /* Firefox */
details summary::-webkit-details-marker { display: none; } /* Chrome */
details summary::marker { display: none; }