@use '../base/variables' as v;

.card {
  background-color: white;
  width: clamp(33rem, 92rem, 70%);
  margin: 24rem auto;
  position: relative;
  display: flex;
  flex-direction: row;
  border-radius: 1.9rem;
  box-shadow: 0 2rem 3rem v.$soft-blue-s;

  &_box {
    position: absolute;
    left: -12.5rem;
    top: 41%;
  }

  &_hero {
    display: none;
  }

  &_heroimg {
    padding: 25.5rem 20rem;
    background: 
    url('../images/illustration-woman-online-desktop.svg') left -8.2rem center no-repeat,
    url('../images/bg-pattern-desktop.svg') -58rem -29rem
    no-repeat;
  }

  &_faq {
    width: clamp(28rem, 35rem, 80%);
    // margin: 6.5rem 0 0 7.5rem;
    margin: 6.5rem 7.5rem;
  }

  &_h1 {
    font-size: 3rem;
    padding-bottom: 2rem;
  }
}


details {
  border-bottom: v.$l-grayish-blue solid 1px;
}

summary {
  cursor: pointer;
  font-size: 1.4rem;
  color: v.$vd-greyish-blue;
  margin: 1.5rem 0;
  position: relative;
}

details summary::before {
  content: '';
  padding: 1rem;
  position: absolute;
  right: 0;
  background: url('../images/icon-arrow-down.svg') center no-repeat;
  transform: rotate(0deg);
}

details[open] summary::before {
  content: '';
  background: url('../images/icon-arrow-down.svg') center no-repeat;
  transform: rotate(180deg);
}

details[open] summary {
  font-weight: 700;
  color: v.$vd-desa-blue;
}

summary:hover {
  color: v.$soft-red;
}

p {
  font-size: 1.2rem;
  color: v.$d-grayish-blue;
  padding-bottom: 2rem;
}

.attribution {
  width: 35rem;
  margin: 5rem auto;
  padding: 2rem;
  text-align: center;
  border-radius: 1rem;
  color: v.$d-grayish-blue;
  background-color: v.$vd-desa-blue;
  opacity: .7;
}

.logo_ms {
  display: inline-block;
  width: 2rem;
  margin: auto;
  margin-left: 1rem;
  vertical-align: middle;

  &-svg {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

a:hover {
  opacity: .5;
}

@media screen and (max-width: 80rem) {

  .card {
    margin: 20rem auto;
    flex-direction: column;
    align-items: center;

    
    &_box {
      display: none;
    }

    &_hero {
      display: block;
      width: 24rem;
      position: absolute;
      top: -10.5rem;
    }

    &_heroimg {
      padding: 6rem 20rem;
      background: url('../images/bg-pattern-mobile.svg') top center no-repeat;
    }

    &_faq {
      margin: 1.5rem auto 4.5rem auto;
    }

    &_h1 {
      text-align: center;
    }

  }

  summary {
    font-size: 1.35rem;
  }
  
  p {
    margin-right: 3rem;
  }

  
}